body {
  font-family: 'Noto Sans KR';
  background-color: #f8f7f7;
}

.App {
  text-align: center;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.section-wrap {
  padding: 1.25rem;
  justify-content: center;
  /* margin: 0 auto; */
}

/* pc버전 레이아웃 */
@media (min-width: 992px) {
  .Section-title {
    height: 3rem;
  }
  .main-img-container {
    width: 50%;
  }
  .main-text-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 3rem;
    margin-bottom: 3.5rem;
  }

  .section-title-container {
    margin-top: 5vw;
    margin-bottom: 2vw;
    display: flex;
    justify-content: flex-start;
  }

  .footer {
    display: flex;
    flex-direction: row;
    text-align: left;
    padding: 4vw 8vw;
    background-color: black;
    height: 30vh;
    color: rgba(255, 255, 255, 0.5);
  }

  .footer-logo-container {
    margin-left: auto;
    margin-top: auto;
  }

  .footer img {
    width: 8vw;
    max-width: 60px;
    margin-right: 0.5rem;
  }

  .project-detail-container {
    padding: 2rem;
  }
  .project-detail-img {
    padding: 0;
    margin: auto 2.5rem;
    /* width: 40%; */
  }
  .carousel {
    overflow: hidden;
  }

  .carousel-inner {
    height: 57vw;
  }

  .edu-wrap {
    display: flex;
    flex-direction: row;
  }

  .edu-container {
    background-color: white;
    margin-left: 1vw;
    min-width: 700px;
    /* flex-direction: ; */
    /* text-align: center; */
    aspect-ratio: 5/3;
    border-radius: 1rem;
    margin-left: auto;
    padding: 2rem;
    box-shadow: 0 30px 80px 0 rgba(0, 0, 0, 0.1);
  }

  .font-xl {
    margin-bottom: 0;
    font-size: 3rem;
    font-weight: 800;
  }
  .font-lg {
    margin-bottom: 0;
    font-size: 2.5rem;
    font-weight: 600;
  }

  .font-md {
    margin-bottom: 0;
    font-size: 1.5rem;
  }
  .font-sm {
    margin-bottom: 0;
    font-size: 1.3rem;
  }
}
/* 태블릿 레이아웃 */
@media screen and (min-width: 576px) and (max-width: 991px) {
  .Section-title {
    height: 2.5rem;
  }

  .section-title-container {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }

  .main-text-container {
    margin-top: 4rem;
    margin-bottom: 4.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .footer {
    display: flex;
    flex-direction: column;
    text-align: left;
    padding: 2rem 5rem;
    background-color: black;
    height: 15rem;
    color: rgba(255, 255, 255, 0.5);
  }
  .footer-logo-container {
    margin-top: auto;
  }

  .footer img {
    width: 5vw;
    margin-right: 0.5rem;
  }

  .project-detail-container {
    padding: 2rem;
  }

  .project-detail-img {
    padding: 0;
    width: 80%;
    margin: auto;
  }
  .carousel-inner {
    height: 76vh;
    margin: 0 auto 5rem;
    width: 100%;
  }

  .carousel-indicators {
    bottom: -4.5rem;
  }

  .carousel-control-prev {
    left: -5rem;
  }

  .carousel-control-next {
    right: -5rem;
  }

  .edu-wrap {
    display: flex;
    flex-direction: column;
  }
  .edu-container {
    background-color: white;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 2rem;
    border-radius: 0.75rem;
    box-shadow: 0 30px 80px 0 rgba(0, 0, 0, 0.1);
  }

  .font-xl {
    margin-bottom: 0;
    font-size: 2.25rem;
    font-weight: 800;
  }
  .font-lg {
    margin-bottom: 0;
    font-size: 1.75rem;
    font-weight: 600;
  }

  .font-md {
    margin-bottom: 0;
    font-size: 1.25rem;
  }
  .font-sm {
    margin-bottom: 0;
    font-size: 1rem;
  }
}

/* 모바일버전 레이아웃 */
@media (max-width: 575px) {
  .Section-title {
    height: 2.5rem;
  }

  .main-text-container {
    width: 100%;
    margin-top: 3rem;
    margin-bottom: 3.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .section-title-container {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .footer {
    display: flex;
    flex-direction: column;
    text-align: left;
    padding: 5vw 8vw;
    background-color: black;
    height: 10rem;
    color: rgba(255, 255, 255, 0.5);
  }

  .footer-logo-container {
    margin-top: auto;
  }

  .footer img {
    width: 8vw;
    max-width: 60px;
    margin-right: 0.5rem;
  }
  .project-detail-container {
    padding: 2rem 0.5rem;
  }
  .project-detail-img {
    padding: 0;
    width: 80%;
    margin: auto;
  }

  .carousel-inner {
    height: 72vh;
  }

  .carousel-control-prev {
    left: -2.5rem;
  }

  .carousel-control-next {
    right: -2.5rem;
  }

  .edu-wrap {
    display: flex;
    flex-direction: column;
  }

  .edu-container {
    background-color: white;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 1.5rem;
    border-radius: 0.5rem;
    box-shadow: 0 30px 80px 0 rgba(0, 0, 0, 0.1);
  }

  /* 폰트 스타일 */
  .font-xl {
    margin-bottom: 0;
    font-size: 1.5rem;
    font-weight: 800;
  }
  .font-lg {
    margin-bottom: 0;
    font-size: 1.2rem;
    font-weight: 600;
  }

  .font-md {
    margin-bottom: 0;
    font-size: 1rem;
  }
  .font-sm {
    margin-bottom: 0;
    font-size: 0.8rem;
  }
}

.size {
  width: 160px;
  height: 160px;
}

span.center {
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  text-align: center;
  line-height: 80px;
  font-size: 20px;
  transform: translate(-50%, -50%);
}

.projects-card-container {
  padding: 1rem;
}

.projects-card {
  background-color: black;
  width: 100%;
  border-radius: 1rem;
  padding: 1rem;
  aspect-ratio: 3/ 4;
}

.projects-card-inner {
  background-color: white;
  width: 100%;
  height: 70%;
  border-radius: 0.5rem;
}

.projects-card-container {
  /* 하단 텍스트 박스 */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30%;
}
.projects-card-text {
  /* 하단 텍스트 속성 */
  text-align: center;
  color: white;
  margin: 0;
  font-weight: 700;
}

.project-detail-container {
  background-color: white;
  border-radius: 1rem;
  box-shadow: 0 5px 40px 0 rgba(0, 0, 0, 0.1);
  margin: auto;
  width: 100%;
  aspect-ratio: 4/2;
  text-align: left;
}

.skill-stack {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  /* height: 3rem; */
  background-color: #00000005;
  border-radius: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.07);
  padding: 0.2rem 0.8rem;
}

.skill-stack img {
  height: 2.25rem;
  margin-right: 0.5rem;
}

.vol {
  padding: 0.5rem 1rem;
  margin-right: 1rem;
  background-color: black;
  color: white;
  margin-bottom: 0;
}

.project-detail-title {
  border-bottom: 1px solid black;
  margin: 0;
}

.works-project-list {
  background-color: white;
  /* height: 6rem; */
  /* border: 1px solid grey; */
  border-radius: 1vw;
  /* max-width: 600px; */
  aspect-ratio: 5/1;
  box-shadow: 0 5px 40px 0 rgba(0, 0, 0, 0.1);
  white-space: pre-line;
}

.acv-container {
  background-color: white;
  aspect-ratio: 1/1;
  width: 40vw;
  max-width: 500px;
  margin: 1vw;
  padding: 1vw;
  border-radius: 2vw;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
}

.acv-img-container {
  display: flex;
  background-color: rgb(250, 248, 246);
  aspect-ratio: 4/3;
  padding: 0 0.5rem;
  border-radius: 1vw;
}

.acv-img-container img {
  object-fit: contain;
}

.nav-pills {
  --bs-nav-pills-border-radius: 0.375rem;
  --bs-nav-pills-link-active-color: #fff;
  --bs-nav-pills-link-active-bg: #ff335c;
  --bs-navbar-active-color: #ffffff;
}

.v-line {
  border-left: 1px solid rgba(255, 255, 255, 0.6);
  height: 50px;
}
