  overflow: hidden;
}

.carousel-inner {
  height: 1060px;
  top: -100px;
} */

.carousel-dark .carousel-indicators [data-bs-target] {
  background-color: white;
  opacity: 0.4;
}
.carousel-dark .carousel-indicators .active {
  opacity: 1;
}

.project-detail-img {
  background-color: black;
}

